.container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  padding: 20px;
}

.crypto-card {
  background-color: #1a1d23;
  border-radius: 12px;
  padding: 20px;
  transition: transform 0.2s, box-shadow 0.2s;
  border: 1px solid #3a3f4b;
  color: white;
  text-decoration: none;
}

.crypto-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.crypto-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
  width: 100%;
}

.crypto-header img {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

.crypto-header h3 {
  margin: 0;
  font-size: 1.2rem;
  white-space: nowrap;
}

.crypto-details {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.price-container,
.transactions-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid #3a3f4b;
}

.label {
  color: #6c757d;
  font-size: 0.9rem;
}

.value {
  font-weight: 600;
  color: #ffffff;
}

.loading-spinner {
  text-align: center;
  color: #6c757d;
  padding: 10px;
}

/* Remove default link styling */
a {
  text-decoration: none;
  color: inherit;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .card-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 15px;
    padding: 10px;
  }

  .crypto-card {
    padding: 15px;
  }
}

/* Add these new styles to your existing CSS */

.name-price-change {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-grow: 1;
}

.price-change {
  font-size: 0.9rem;
  font-weight: 500;
  padding: 2px 6px;
  border-radius: 4px;
  min-width: 65px;
  text-align: right;
}

.price-change.positive {
  color: #00ff88;
}

.price-change.negative {
  color: #ff4d4d;
}

.home-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 20px;
  background: #2a2a2a;
  border-radius: 12px;
}

.title-section h2 {
  margin: 0;
  color: #fff;
}

.aggregate-stats {
  display: flex;
  gap: 30px;
}

.stat-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}

.stat-label {
  color: #8a8f98;
  font-size: 0.9rem;
}

.stat-value {
  font-size: 1.1rem;
  font-weight: 600;
  color: #fff;
}

.stat-value.positive {
  color: #28a745;
}

.stat-value.negative {
  color: #dc3545;
}

.stat-tooltip {
  visibility: hidden;
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  color: white;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 14px;
  white-space: nowrap;
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.3s;
  margin-bottom: 10px;
  width: max-content;
}

.stat-tooltip::before {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border-width: 5px;
  border-style: solid;
  border-color: #333 transparent transparent transparent;
}

.stat-item:hover .stat-tooltip {
  visibility: visible;
  opacity: 1;
}

/* Mobile responsiveness */
@media screen and (max-width: 768px) {
  .home-header {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }

  .aggregate-stats {
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }
}